let list = document.querySelectorAll(".list");
for(let i=0;i<list.length;i++){
    list[i].onmouseover = function(){
        let j = 0;
        while(j<list.length){
            list[j++].className = 'list';
        }
        list[i].className = 'list active';
        if(list[i].id == "Friends") {
            console.log("Friends detected");
            addListFriends(friendsarr);
        }
        else if(list[i].id == "Add"){
            console.log("Add Friends detected");
            addListUser(userarr);
        }
        else if(list[i].id == "Notification"){
            console.log("Notifications detected");
            addList(arr);
        }
    }
}



