const username = "";



document.getElementById("UsernameButtonLi").addEventListener("mousedown", function(username){
    var InputUsername = document.getElementById("Inputusername").textContent;
    var legnth = InputUsername.length;
    if(InputUsername == '') {
        console.log("Darf nicht leer sein");
    }
    else if(InputUsername.replace('"', '').length < legnth){   
        console.log('kein "');
    }
    else if(InputUsername.replace(/\s/g, '').length < legnth){   
        console.log('kein Leerzeichen1');
    }
    else if(InputUsername.replace(/ /g, '').length < legnth){   
        console.log('kein Leerzeichen2');
    }
    else {
        console.log("hat funktioniert");
        username = InputUsername;
        console.log(username);
        firebaseRef = firebase.database().ref("Users/"+username);
        firebaseRef.update({
            "Status" : "online",
        });
        document.getElementById("UsernameButtonUl").style.display = "none";
        document.getElementById("Inputusername").style.display = "none";
        var TimeoutMouseevent = window.setTimeout(function() {
            firebaseRef = firebase.database().ref("Users/"+username);
            firebaseRef.update({
                "Status" : "away",
            });
        },10000);
        firebaseRef = firebase.database().ref("Users/"+username);
        firebaseRef.onDisconnect().update({
            "Status" : "offline",
        });
        window.addEventListener('mousemove', function(){
            console.log("mousmove erkannt")
            firebaseRef = firebase.database().ref("Users/"+username);
            firebaseRef.update({
                "Status" : "online",
            });
            clearTimeout(TimeoutMouseevent);
            clearTimeout(TimeoutMouseevent2);
            var TimeoutMouseevent2 = window.setTimeout(function() {
                firebaseRef = firebase.database().ref("Users/"+username);
                firebaseRef.update({
                    "Status" : "away",
                });
            },10000);
        });

    }
});


    
firebaseRef = firebase.database().ref("Users");

var userarr = [];

firebaseRef.on("value",function(data){
    vl = data.val();
    userarr = Object.keys(vl);
    
});


function addListUser(userarr){
    
    document.querySelector(".notCompleted").innerHTML = "";

    document.getElementById("Heading").innerHTML = "Users";

    userarr.forEach(function(e){
        const notCompleted = document.querySelector('.notCompleted');
	    const Completed = document.querySelector('.Completed');

	    const newLi = document.createElement('li');
	    const checkBtn = document.createElement('button');
	    const delBtn = document.createElement('button');

	    checkBtn.innerHTML = '<i class="fas fa-user-plus"></i>';

        newLi.textContent = e;
		notCompleted.appendChild(newLi);
		newLi.appendChild(checkBtn);
		newLi.appendChild(delBtn);


        checkBtn.addEventListener('click', function(){
            const parent = this.parentNode;
            parent.remove();
            //Completed.appendChild(parent);
            checkBtn.style.display = 'none';
            const tag = parent.textContent;
            firebaseRef = firebase.database().ref("Users/"+tag+"/Notifications");
            firebaseRef.update({
                [username]  : "1",
            });
        });
    });

}




