function addList(arr){
    
    document.querySelector(".notCompleted").innerHTML = "";

    document.getElementById("Heading").innerHTML = "Friend Requests";

    arr.forEach(function(e){
        const notCompleted = document.querySelector('.notCompleted');
	    const Completed = document.querySelector('.Completed');

	    const newLi = document.createElement('li');
	    const checkBtn = document.createElement('button');
	    const delBtn = document.createElement('button');

	    checkBtn.innerHTML = '<i class="fa fa-check"></i>';
	    delBtn.innerHTML = '<i class="fa fa-trash"></i>';

        newLi.textContent = e;
		notCompleted.appendChild(newLi);
		newLi.appendChild(checkBtn);
		newLi.appendChild(delBtn);


        checkBtn.addEventListener('click', function(){
            const parent = this.parentNode;
            parent.remove();
            //Completed.appendChild(parent);
            checkBtn.style.display = 'none';
            const tag = parent.textContent;
            firebaseRef = firebase.database().ref("Users/"+username+"/Friends");
            firebaseRef.update({
                [tag]  : "1",
            });
            firebaseRef = firebase.database().ref("Users/"+username+"/Notifications/"+tag);
            firebaseRef.remove();

            firebaseRef = firebase.database().ref("Users/"+tag+"/Friends");
            firebaseRef.update({
                [username] : "1",
            });
        });
    
        delBtn.addEventListener('click', function(){
            const parent = this.parentNode;
            parent.remove();
            const tag = parent.textContent;
            firebaseRef = firebase.database().ref("Users/"+username+"/Notifications/"+tag);
            firebaseRef.remove();
        });
    });

}




firebaseRef = firebase.database().ref("Users/"+username+"/Friends");

var friendsarr = [];

firebaseRef.on("value", function(data){
    valb = data.val();
    friendsarr = Object.keys(valb);
    console.log(friendsarr);
});



function addListFriends(friendsarr){
    
    document.querySelector(".notCompleted").innerHTML = "";

    document.getElementById("Heading").innerHTML = "Friends";

    friendsarr.forEach(function(e){
        const notCompleted = document.querySelector('.notCompleted');
	    const Completed = document.querySelector('.Completed');

	    const newLi = document.createElement('li');
        const statusBtn = document.createElement('button');
	    const checkBtn = document.createElement('button');
	    const delBtn = document.createElement('button');
        statusBtn.id = "Status";

        firebaseRef = firebase.database().ref("Users/"+e);

        firebaseRef.child("Status").on("value", function(data){
            valb = data.val();
            console.log(valb);
            if(valb == "online"){
                statusBtn.style.color = "green";
            }
            else if(valb == "offline"){
                statusBtn.style.color = "grey";
            }
            else if(valb == "away"){
                statusBtn.style.color = "orange";
            }
        });
    

	    checkBtn.innerHTML = '';
	    delBtn.innerHTML = '';
        statusBtn.innerHTML = '<i class="fas fa-circle"></i>';
        newLi.textContent = e;
		notCompleted.appendChild(newLi);
		newLi.appendChild(checkBtn);
		newLi.appendChild(delBtn);
        newLi.appendChild(statusBtn);


        checkBtn.addEventListener('click', function(){
            const parent = this.parentNode;
            parent.remove();
            //Completed.appendChild(parent);
            checkBtn.style.display = 'none';
            const tag = parent.textContent;
            firebaseRef = firebase.database().ref("Users/"+username+"/Friends");
            firebaseRef.update({
                [tag]  : "1",
            });
            firebaseRef = firebase.database().ref("Users/"+username+"/Notifications/"+tag);
            firebaseRef.remove();
        });
    
        delBtn.addEventListener('click', function(){
            const parent = this.parentNode;
            parent.remove();
            const tag = parent.textContent;
            firebaseRef = firebase.database().ref("Users/"+username+"/Notifications/"+tag);
            firebaseRef.remove();
        });
    });

}
















